





















import Vue from 'vue';

export default Vue.extend({
  computed: {
    showModal: {
      get() {
        return this.$store.getters.isSignInModalDisplayed;
      },
      set() {
        this.$store.commit('TOGGLE_SIGN_IN_MODAL');
      },
    },
  },
  methods: {
    SignInWithGithub(evt) {
      window.location.href = '/auth';
    },
  },
});
